.contact {
  margin-top: 70px;
  padding: 3.5rem 0;
  background: linear-gradient(135deg, var(--primary-dark), var(--accent-dark))
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  align-items: start
}

.contact-info h3 {
  font-weight: 300;
  margin-bottom: .5rem
}

.contact-details {
  display: flex;
  flex-direction: column;
  gap: .75rem;
  margin-top: 1rem
}

.contact-item {
  display: flex;
  gap: .75rem;
  align-items: center
}

.contact-icon {
  width: 44px;
  height: 44px;
  background: rgba(0, 212, 255, 0.08);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  border: 1px solid var(--primary-cyan)
}

.contact-form {
  background: rgba(20, 24, 41, 0.5);
  padding: 1rem;
  border-radius: 12px;
  border: 1px solid rgba(0, 212, 255, 0.06)
}

.form-group {
  margin-bottom: .75rem
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: .6rem;
  border-radius: 8px;
  background: var(--hover-dark);
  border: 1px solid rgba(0, 212, 255, 0.06);
  color: var(--text-primary)
}

.form-submit {
  background: linear-gradient(90deg, var(--dark-cyan), var(--primary-cyan));
  color: var(--primary-dark);
  padding: .8rem;
  border-radius: 999px;
  border: none;
  width: 100%;
  cursor: pointer
}

/* Responsive adjustments */

@media(max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
  }

  .contact-form {
    margin-top: 1rem;
  }
}